@import url('https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap');

#clock
{
  width: 90%;
  max-width: 500px;
  text-align: center;
  font-family: 'Times New Roman', Times, serif;
  /* font-family: 'Share Tech Mono', monospace; */
  color: white;
}
#main-title
{
  font-size: 3rem;
  font-weight: 400;
  margin-bottom: 20px;
}
button
{
  background: none;
  outline: none;
  border: none;
  color: white;
  cursor: pointer;
}
a
{
  text-decoration: none;
  color: inherit;
}
#author
{
  margin-top: 20px;
  font-size: 1.2rem;
}
@media screen and (max-width: 768px)
{
  html
  {
    font-size: 50%;
  }
}
