.timer
{
  border: 7px solid #13353a;
  margin: 20px auto 10px auto;
  border-radius: 50px;
  width: 60%;
  height: 180px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}
#timer-label
{
  font-size: 2rem;
}
#timer-left
{
  font-size: 5rem;
}

@media screen and (max-width: 768px)
{
  .timer
  {
    width: 40%;
    height: 120px;
    border-radius: 25px;
  }
}